import React from 'react';

export default function ButtonsVideo({
	setVideoPlay,
	videoPlay,
}) {
	return (
		<>
			{videoPlay ? (
				<button
					type="button"
					className="btn btn-warning"
					onClick={(e) => {
						setVideoPlay(!videoPlay);
					}}
				>
					Pausar
				</button>
			) : (
				<button
					type="button"
					className="btn btn-success"
					onClick={(e) => {
						setVideoPlay(!videoPlay);
					}}
				>
					Iniciar
				</button>
			)}
		</>
	);
}
