import React, { useMemo, useEffect } from 'react';
import { VideoTexture, LinearFilter, RGBFormat } from 'three';

export default function Video({
	url,
	position,
	state,
	setState,
}) {
	const video = useMemo(() => {
		console.log(url);
		const vid = document.createElement('video');
		vid.src = url;
		vid.crossOrigin = 'Anonymous';
		vid.pause();
		return vid;
	}, [url]);

	const texture = useMemo(() => {
		const t = new VideoTexture(video);
		t.flipY = true;
		t.minFilter = LinearFilter;
		t.magFilter = LinearFilter;
		t.format = RGBFormat;
		return t;
	}, [video]);

	useEffect(() => {
		state ? video.play() : video.pause();
	}, [state, video]);

	return (
		<mesh
			position={position}
			onClick={(e) => {
				console.log('holi');
				setState(!state);
			}}
			onMouseDown={(e) => {
				console.log('holi');
				setState(!state);
			}}
		>
			<planeBufferGeometry attach="geometry" args={[3, 2]} />
			<meshBasicMaterial attach="material" map={texture} />
		</mesh>
	);
}
